import { useState } from "react";
import { motion } from "framer-motion";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination, Autoplay } from "swiper/modules";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import cx from "@/modules/portfolio/styles/Project.module.scss";
import accessimg from "@/assets/access.png";
import realphimg from "@/assets/realph.png";
import qtimeimg from "@/assets/qtime.png";
import meappimg from "@/assets/meapp.png";

const Projects = () => {
  const [currentSlide, setCurrentSlide] = useState(0);

  const projects = [
    {
      title: "Access Website Builder",
      desc: "A website builder web application to create beautiful and functional real-estate websites.",
      img: accessimg,
      link: "https://access.com/",
      technologies: ["React/Next", "Typescript", "Prisma", "PostgreSQL", "Tailwind CSS"],
    },
    {
      title: "Real.ph",
      desc: "A real-estate website which is an online marketplace for property listings in the Philippines.",
      img: realphimg,
      link: "https://real.ph/",
      technologies: ["Laravel", "MySQL", "Redis", "AWS"],
    },
    {
      title: "Me App Instant ID Maker",
      desc: "Instant ID Maker",
      img: meappimg,
      link: "http://me.forge99.com/",
      technologies: ["Laravel", "Vue.js", "Vuex", "MySQL"],
    },
    {
      title: "Qtime",
      desc: "A time-tracking web application software for remote teams.",
      img: qtimeimg,
      link: "https://qtimesoftware.com/",
      technologies: ["Laravel", "Vue.js", "PostgreSQL"],
    },
  ];

  return (
    <section className="mb-20">
      <h2 className="text-3xl font-semibold mb-10 text-center">
        Projects Involved
      </h2>

      <Swiper
        modules={[Navigation, Pagination, Autoplay]}
        spaceBetween={30}
        slidesPerView={1}
        navigation
        pagination={{ clickable: true }}
        autoplay={{
          delay: 5000,
          disableOnInteraction: false,
        }}
        loop={true}
        breakpoints={{
          744: {
            slidesPerView: 2,
          },
          1200: {
            slidesPerView: 3,
          },
        }}
        onSlideChange={(swiper) => setCurrentSlide(swiper.activeIndex)}
        className={cx["swiper-projects"]}
      >
        {projects.map((project, i) => (
          <SwiperSlide key={i}>
            <motion.div
              initial={{ opacity: 0, y: 30 }}
              whileInView={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.4, delay: i * 0.1 }}
              viewport={{ once: true }}
              className="h-full"
            >
              <div className="bg-white rounded-2xl shadow-md hover:shadow-lg transition p-5 dark:bg-stone-300 dark:hover:bg-white h-full min-h-[30rem] flex flex-col">
                <img
                  src={project.img}
                  alt={project.title}
                  className="w-full h-64 object-cover rounded-md mb-4 object-top"
                />
                <h3 className="text-xl font-bold mb-4 text-gray-800">
                  {project.title}
                </h3>

                <div className="mb-4 flex-grow">
                  <h4 className="font-semibold mb-2 text-gray-700">
                    Technologies:
                  </h4>
                  <div className="flex flex-wrap gap-2">
                    {project.technologies.map((tech, techIndex) => (
                      <span
                        key={techIndex}
                        className="px-2 py-1 bg-blue-100 text-blue-800 rounded-full text-xs"
                      >
                        {tech}
                      </span>
                    ))}
                  </div>
                </div>

                <a
                  href={project.link}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="inline-block mt-auto text-sm font-medium text-blue-600 hover:underline"
                >
                  View more 
                </a>
              </div>
            </motion.div>
          </SwiperSlide>
        ))}
      </Swiper>
      <div className="flex justify-center mt-10">
        {projects.map((_, index) => (
          <button
            key={index}
            className={`pagination-dot ${
              currentSlide === index ? "active" : ""
            }`}
            onClick={() => setCurrentSlide(index)}
          >
            <span></span>
          </button>
        ))}
      </div>
    </section>
  );
};

export default Projects;
